import './idp-not-setup.scss'
import React from 'react';
import {
    Title,
    Text,
    Button
} from '@patternfly/react-core';
import CubesIcon from '@patternfly/react-icons/dist/esm/icons/cubes-icon';
import PropTypes from "prop-types";

interface Props{
    setupIDP: any,
    wizardEnabled: boolean
}

const IdpNotSetup = ({setupIDP, wizardEnabled}: Props) => {

    return <div className="notSetupDiv">
                <CubesIcon className="cubesIcon"/>
                <Title headingLevel="h2" size="xl" className="pf-v5-u-mt-lg">No Identity Providers are currently configured</Title>
                <Text className="pf-v5-u-mt-md">Your organization has not integrated an identity provider. More information is available <Button variant="link" target="_blank" href="https://access.redhat.com/announcements/7039300" component="a" isInline>here</Button></Text>
                <Button className={wizardEnabled ? "pf-v5-u-mt-lg" : "hideFeature"} variant="primary" onClick={setupIDP}>Set up an identity provider</Button>
            </div>
}

IdpNotSetup.propTypes = {
    setupIDP: PropTypes.func
}

export default IdpNotSetup;
