import React, {MutableRefObject} from 'react';
import { WizardFooterWrapper /* data-codemods */, useWizardContext /* data-codemods */
} from '@patternfly/react-core/dist/esm/components/Wizard';
import { Button } from '@patternfly/react-core';
import {getSSOApiUrl} from "../../../Utils/general-utils";
import {useChrome} from "@redhat-cloud-services/frontend-components/useChrome";
import {SubmittableChoices, UserChoices} from "@apps/IdpSelfService/CommonInterfaces/user-choices";

import PropTypes from "prop-types";

interface Props{
    userChoiceData: MutableRefObject<UserChoices>;
    previous: Function;
    accessToken: string;
}

const SubmitFooter = ({userChoiceData, previous, accessToken}: Props) => {
    const { goToPrevStep } = useWizardContext();
    const [isLoading, setIsLoading] = React.useState(false);


    /**
     * convert the choice data to use the key's required by the api.
     */
    const filterChoiceData = (): SubmittableChoices => {

        const provider = userChoiceData.current.selections.protocol === "OIDC_CONFIG" ? "oidc-improved" : "saml-improved"
        const idpEID = "https://sso.redhat.com/auth/realms/redhat-external";
        const reqURL = "https://sso.redhat.com/auth/realms/redhat-external/broker/rhtest/endpoint";
        const cert = "dafad";

        return {
            providerId: provider,
            identityProviderEntityId: idpEID,
            authenticationRequestsUrl: reqURL,
            x509Certificate: cert
        }
    }


    const apiUrl = getSSOApiUrl(useChrome(), 'apis/organizations/v1/identity-providers')
    const createIDPCall = () => {
        fetch(apiUrl, {
            method: 'POST',
            referrerPolicy: 'no-referrer',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + accessToken
            },
            body: JSON.stringify(filterChoiceData())
        })
            .then(response => response.json())
            .then(() => {
                setIsLoading(false)
                window.location.reload();
            })
            .catch(error => {
                // this should be exchanged for an error message somewhere.
                console.error('Error: ', error);
                setIsLoading(false);
            })
    }

    /**
     * Governs what happens when the next button gets clicked. Important for allowing the back and forth communication
     * between the footer element and the actual component being displayed when needing to perform steps such as validation.
     */
    const handleNextClick = () => {
        const stringifySpace = 2;
        console.log("Submitting with this data: " + JSON.stringify(userChoiceData, null, stringifySpace));

        // trigger the spinner.
        setIsLoading(true);
        // fire the call to SSO.
        createIDPCall();
    }

    const handleBackClick = () => {
        goToPrevStep();
        // custom logic to update the current flow state.
        previous();
    }

    return (
        <WizardFooterWrapper>
            <Button onClick={handleNextClick} isLoading={isLoading} >Next</Button>
            <Button variant="secondary" onClick={handleBackClick}>
                Back
            </Button>
        </WizardFooterWrapper>
    );
}


SubmitFooter.propTypes = {
    userChoiceData: PropTypes.object,
}

export default SubmitFooter;
