// step one of the flow https://miro.com/app/board/uXjVO_x__QE=/

import React, {useState} from 'react';
import { 
    Text,
    TextInput,
    Radio,
    Title,
    Form,
    FormGroup,
    HelperText,
    HelperTextItem
} from '@patternfly/react-core';
import {processAllInputs} from '../../../Utils/general-utils';
import PropTypes from 'prop-types';

interface Props {
    resetNextClicked: Function;
    rootUserChoices: any;
    rootValidation: any;
    updateStatus: Function;
}

/**
 * handle input selection needs to update information being used to govern the application flow as well as the final api update object
 * (if needed this method could be shared between components and moved to general-utils.ts)
 */
export const handleInputSelection = (value: any, rootUserChoices: any, validations: any, setValidations: any, rootValidation: any, updateStatus: any, resetNextClicked: any) => {

    /*
     * regardless of current state. if the user alters the input clear existing flags. \
     * This should also reset the currently displayed errors
     */
    resetNextClicked(false);

    if (!rootUserChoices.current) {
        rootUserChoices.current = {};
    }

    if (!rootUserChoices.current.selections) {
        rootUserChoices.current.selections = {};
    }

    //  update the main data storage object with the new value
    rootUserChoices.current.selections[value.target.id] = value.target.value;

    /*
     * update the validation settings. It is expected that the id's used in the validation object matches
     * the id's used on the elements.
     */
    // @ts-ignore
    const failureTriggered = processAllInputs(rootUserChoices.current.selections, validations, setValidations);
    const failureStore = 'CUST_INFO'

    if (!rootValidation.current) {
        rootValidation.current = {};
    }

    rootValidation.current[failureStore] = !failureTriggered;

    if (!failureTriggered && rootUserChoices.current.selections.protocol) {
        updateStatus(rootUserChoices.current.selections.protocol);
    }
};

const CustomerInformation = ({resetNextClicked, rootUserChoices, rootValidation, updateStatus} : Props) => {
    const [validations, setValidations] = useState({
        'protocol': {
            inputType: 'RADIO',
            status: 'default',
            message: ''
        }
    });

    const handleChange = (value: any) => {
        handleInputSelection(value, rootUserChoices, validations, setValidations, rootValidation, updateStatus, resetNextClicked);
    };

    // pull from props.inputTrigger to handle change values
    return <Form>
                <Title headingLevel="h2">Customer Information</Title>
                <Text className="pf-v5-u-color-400 pf-v5-u-font-size-sm">All fields are required</Text>

                {/* TODO had to remove validated prop. PF5 suggests moving that logic to the text input's helperText, FormHelperText, or HelperTextItem */}

                <FormGroup
                    onChange={(value) => { handleChange(value) } }
                    role="radiogroup"
                    fieldId="authType"
                    label="Choose identity provider protocol"
                    id="protocol">
                    <Radio name="authType" id="protocol" label="OpenID Connect" value="OIDC_CONFIG"/>
                    <Radio name="authType" id="protocol" label="Saml 2.0" value="SAML_CONFIG"/>
                </FormGroup>
            </Form>
}

CustomerInformation.propTypes = {
    resetNextClicked: PropTypes.func,
    rootUserChoices: PropTypes.object,
    rootValidation: PropTypes.object,
    nextClicked: PropTypes.func,
    updateStatus: PropTypes.func,
}

export default CustomerInformation;
